import React from 'react';
import moment from 'moment';
import Datepicker from 'react-datepicker';
import { BsCalendar } from 'react-icons/bs';

import './Calendar.scss';

type Props = {
  endDate: Date;
  startDate: Date;
  setEndDate: (date: Date) => void;
  setStartDate: (date: Date) => void;
};

const Calendar: React.FC<Props> = ({
  endDate,
  startDate,
  setStartDate,
  setEndDate,
}) => {
  return (
    <div className="StatementsCalendar">
      <div className="SelectedDate">
        {moment(startDate).format('ddd, DD MMMM')}
        <BsCalendar />
        {moment(endDate).format('ddd, DD MMMM')}
      </div>
      <div className="Wrapper">
        <Datepicker
          selected={startDate}
          startDate={startDate}
          endDate={endDate}
          inline
          selectsStart
          onChange={(date: Date) => setStartDate(date)}
        />
        <Datepicker
          selected={endDate}
          startDate={startDate}
          endDate={endDate}
          minDate={startDate}
          inline
          selectsEnd
          onChange={(date: Date) => setEndDate(date)}
        />
      </div>
    </div>
  );
};

export default Calendar;
