/* eslint-disable import/no-cycle */
import handlePublicRequest from '../utils/handlePublicRequest';
import URL from '../utils/url';

import type { Response } from '../utils/types';
import type { FullRegisterDTO, RestorePasswordType, SignInType } from './types';

export const refreshAppToken = (
  accessToken: string,
  refreshToken: string,
): Promise<Response> =>
  handlePublicRequest({
    url: URL('auth/refresh'),
    method: 'POST',
    data: {
      accessToken,
      refreshToken,
    },
  });

export const signIn = ({ email, password }: SignInType): Promise<Response> =>
  handlePublicRequest({
    url: URL('auth/signin'),
    method: 'POST',
    data: {
      email,
      password,
    },
  });

export const signUp = (data: FullRegisterDTO): Promise<Response> =>
  handlePublicRequest({
    url: URL('auth/signup'),
    method: 'POST',
    data: {
      ...data,
      fullPhone: `+${data.countryCode}${data.phone}`,
    },
  });

export const restorePassword = ({
  email,
}: RestorePasswordType): Promise<Response> =>
  handlePublicRequest({
    url: URL('auth/forgot-password'),
    method: 'POST',
    data: {
      email,
    },
  });
