/* eslint-disable import/no-cycle */
import handlePrivateRequest from '../utils/handlePrivateRequest';
import URL from '../utils/url';

import type { Response } from '../utils/types';
import type { EditUserType, InviteUserType, SetUserRoleType } from './types';

export const getAllUsers = (): Promise<Response> =>
  handlePrivateRequest({
    url: URL('users'),
    method: 'GET',
  });

export const getUserData = (): Promise<Response> =>
  handlePrivateRequest({
    url: URL('users/current'),
    method: 'GET',
  });

export const getUserDataById = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`users/${id}`),
    method: 'GET',
  });

export const blockUserById = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`users/${id}/block`),
    method: 'PATCH',
  });

export const unblockUserById = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`users/${id}/unblock`),
    method: 'PATCH',
  });

export const setUserRoleById = ({
  id,
  role,
}: SetUserRoleType): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`users/${id}/role`),
    method: 'PATCH',
    data: {
      role,
    },
  });

export const editUserById = (data: EditUserType): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`users/${data.id}`),
    method: 'PATCH',
    data,
  });

export const inviteUser = (data: InviteUserType): Promise<Response> =>
  handlePrivateRequest({
    url: URL('users'),
    method: 'POST',
    data,
  });
