import React, { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import qs from 'query-string';
import { FaAngleDown, FaPause, FaPlay, FaTimes } from 'react-icons/fa';
import { BsSearch } from 'react-icons/bs';
import { Dropdown, Input } from 'semantic-ui-react';

import CardsList from '../CardsList/CardsList';
import CreateCardModal from '../CreateCardModal/CreateCardModal';
import Statements from '../Statements/Statements';

import useActions from './useActions';
import useBootstrap from './useBootstrap';
import useCreateCard from './useCreateCard';
import useRoles from './useRoles';
import useSearch from './useSearch';
import useSelection from './useSelection';
import './Cards.scss';

const Cards: React.FC = () => {
  const { search } = useLocation();
  const params = qs.parse(search);

  const {
    batchBlock,
    batchPause,
    batchUnpause,
    block,
    getHistory,
    pause,
    unpause,
  } = useActions();
  const { bootstrap, list } = useBootstrap();
  const { handleChange } = useSearch();
  const { selectedIds, select, selectAll } = useSelection(
    list.map((item) => item.id),
  );
  const {
    createModal,
    handleCloseModal,
    handleOpenModal,
    handleCreateNewCard,
  } = useCreateCard();
  const { adminLike, notBuyer } = useRoles();

  useEffect(() => {
    if (!list.length) {
      bootstrap();
    }
  }, []);

  return (
    <section className="Cards">
      <header>
        <Link
          className={!params.view || params.view === 'cards' ? 'active' : ''}
          to="?view=cards"
        >
          My Cards
        </Link>
        <Link
          className={params.view === 'statements' ? 'active' : ''}
          to="?view=statements"
        >
          Statements
        </Link>
      </header>
      {!params.view || params.view === 'cards' ? (
        <div>
          <div className="TableHeading">
            <div className="Actions">
              <Dropdown
                icon={<FaAngleDown />}
                text="Actions"
                disabled={!selectedIds.length}
              >
                <Dropdown.Menu>
                  <Dropdown.Item
                    icon={<FaPause />}
                    text="Pause"
                    onClick={() => batchPause(selectedIds)}
                  />
                  <Dropdown.Item
                    icon={<FaPlay />}
                    text="Unpause"
                    onClick={() => batchUnpause(selectedIds)}
                  />
                  <Dropdown.Item
                    icon={<FaTimes />}
                    text="Block"
                    onClick={() => batchBlock(selectedIds)}
                  />
                </Dropdown.Menu>
              </Dropdown>
              <Input
                className="CardsSearch"
                type="search"
                placeholder="Search"
                icon={<BsSearch />}
                iconPosition="left"
                onChange={(_, { value }) => handleChange(value)}
              />
            </div>
            <div className="Sorting">
              {notBuyer && (
                <CreateCardModal
                  opened={createModal}
                  handleCloseModal={handleCloseModal}
                  handleOpenModal={handleOpenModal}
                  handleCreateNewCard={handleCreateNewCard}
                />
              )}
            </div>
          </div>
          <CardsList
            adminLike={adminLike}
            items={
              params.status
                ? list.filter((item) => item.status === params.status)
                : list
            }
            selectedItems={selectedIds}
            handleBlockCard={block}
            handlePauseCard={pause}
            handleUnpauseCard={unpause}
            handleGetCardHistory={getHistory}
            handleSelect={select}
            handleSelectAll={selectAll}
          />
        </div>
      ) : (
        <Statements />
      )}
    </section>
  );
};

export default Cards;
