import { useCallback, useState } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { createCard } from '../../api';
import type { CreateCardType } from '../../api';

import useBootstrap from './useBootstrap';

type ReturnType = {
  createModal: boolean;
  handleCloseModal: () => void;
  handleOpenModal: () => void;
  handleCreateNewCard: (data: CreateCardType) => void;
};

const useCreateCard = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const [createModal, setCreateModal] = useState(false);

  const handleCloseModal = useCallback(
    () => setCreateModal(false),
    [setCreateModal],
  );

  const handleOpenModal = useCallback(
    () => setCreateModal(true),
    [setCreateModal],
  );

  const { run: handleCreateCard } = useRequest<string[]>(createCard, {
    manual: true,
  });

  const handleCreateNewCard = useCallback(async (data) => {
    const response = await handleCreateCard(data);

    if (response.length) {
      bootstrap();
      handleCloseModal();
      toast.success('Card(s) created successfully');
    } else {
      toast.error('Cannot create card');
    }
  }, []);

  return {
    createModal,
    handleCloseModal,
    handleOpenModal,
    handleCreateNewCard,
  };
};

export default useCreateCard;
