import React from 'react';
import { BsCalendar } from 'react-icons/bs';
import { Button, Header, Popup, Segment } from 'semantic-ui-react';

import { formatCurrency } from '../../utils/numbers';

import useFilter from './useFilter';
import './Analytics.scss';
import { Calendar } from '../../components';

const Analytics: React.FC = () => {
  const { endDate, fetchData, filter, startDate, setEndDate, setStartDate } =
    useFilter();

  return (
    <section className="Analytics">
      <header>
        <Header as="h1">Analytics</Header>
        <Button.Group size="mini">
          <Popup
            className="CalendarPopup"
            trigger={
              <Button primary={filter === 'custom'}>
                <BsCalendar />
                Custom
              </Button>
            }
            content={
              <Calendar
                endDate={endDate}
                startDate={startDate}
                setEndDate={(date) => {
                  setEndDate(date);
                  fetchData('custom', {
                    type: 'to',
                    value: date,
                  });
                }}
                setStartDate={(date) => {
                  setStartDate(date);
                  fetchData('custom', {
                    type: 'from',
                    value: date,
                  });
                }}
              />
            }
            on="click"
            position="bottom center"
            hoverable
          />
          <Button primary={filter === 'day'} onClick={() => fetchData('day')}>
            Day
          </Button>
          <Button primary={filter === 'week'} onClick={() => fetchData('week')}>
            Week
          </Button>
          <Button
            primary={filter === 'month'}
            onClick={() => fetchData('month')}
          >
            Month
          </Button>
          <Button
            primary={!filter || filter === 'year'}
            onClick={() => fetchData('year')}
          >
            Year
          </Button>
        </Button.Group>
      </header>
      <Segment basic>
        <div className="Holder">
          <div>
            <Header as="h3">All fees by categories</Header>
            <section>
              <div>
                <Header as="h4">Costs</Header>
                <dl>
                  <dt>Cards issuing</dt>
                  <dd>{formatCurrency(1456.43)}</dd>
                </dl>
                <dl>
                  <dt>Transaction cost</dt>
                  <dd>{formatCurrency(132.39)}</dd>
                </dl>
                <dl>
                  <dt>Customers onboarding</dt>
                  <dd>{formatCurrency(54)}</dd>
                </dl>
                <dl>
                  <dt>3D secure checks</dt>
                  <dd>{formatCurrency(0)}</dd>
                </dl>
              </div>
              <div>
                <Header as="h4">Profit</Header>
                <dl>
                  <dt>Cashback</dt>
                  <dd>{formatCurrency(5243.23)}</dd>
                </dl>
              </div>
            </section>
            <div className="Total">
              <span>Total</span> {formatCurrency(3600.41)}
            </div>
          </div>
          <div>
            <Header as="h3">Profit by users</Header>
            <section>
              <div>
                <Header as="h4">User</Header>
                <dl>
                  <dt>First</dt>
                  <dd>{formatCurrency(5464.43)}</dd>
                </dl>
                <dl>
                  <dt>Second</dt>
                  <dd>{formatCurrency(3452.39)}</dd>
                </dl>
                <dl>
                  <dt>Third</dt>
                  <dd>{formatCurrency(311)}</dd>
                </dl>
                <dl>
                  <dt>etc</dt>
                  <dd>{formatCurrency(11000)}</dd>
                </dl>
              </div>
              <div />
            </section>
          </div>
        </div>
      </Segment>
    </section>
  );
};

export default Analytics;
