import React from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { Button, Form, Input } from 'semantic-ui-react';

import { FullRegisterDTO } from '../../api';

type Props = {
  setFormValues: (data: FullRegisterDTO) => void;
  handleSignUpSubmit: (data: FullRegisterDTO) => void;
};

const SignUp: React.FC<Props> = ({ setFormValues, handleSignUpSubmit }) => {
  const initialValues = {
    registrationNumber: '',
    state: '',
    country: '',
    city: '',
    postCode: '',
    address: '',
    address2: '',
  };

  const { errors, touched, values, handleChange, handleSubmit } = useFormik({
    initialValues,
    validationSchema: yup.object({
      registrationNumber: yup.string().min(2).required(),
      state: yup.string().min(2).required(),
      country: yup.string().min(2).required(),
      city: yup.string().min(2).required(),
      postCode: yup.string().min(4).max(6).required(),
      address: yup.string().min(10).required(),
      address2: yup.string(),
    }),
    onSubmit: (data) => {
      setFormValues(data);
      handleSignUpSubmit(data);
    },
  });

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Field>
        <Input
          className={values.registrationNumber ? 'filled' : ''}
          label="Registration number"
          labelPosition="right"
          name="registrationNumber"
          type="text"
          error={Boolean(
            touched.registrationNumber && errors.registrationNumber,
          )}
          value={values.registrationNumber}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.state ? 'filled' : ''}
          label="State"
          labelPosition="right"
          name="state"
          type="text"
          error={Boolean(touched.state && errors.state)}
          value={values.state}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.country ? 'filled' : ''}
          label="Country"
          labelPosition="right"
          name="country"
          type="text"
          error={Boolean(touched.country && errors.country)}
          value={values.country}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.city ? 'filled' : ''}
          label="City"
          labelPosition="right"
          name="city"
          type="text"
          error={Boolean(touched.city && errors.city)}
          value={values.city}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.postCode ? 'filled' : ''}
          label="Post code"
          labelPosition="right"
          name="postCode"
          type="text"
          error={Boolean(touched.postCode && errors.postCode)}
          value={values.postCode}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.address ? 'filled' : ''}
          label="Address"
          labelPosition="right"
          name="address"
          type="text"
          error={Boolean(touched.address && errors.address)}
          value={values.address}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.address2 ? 'filled' : ''}
          label="Address 2"
          labelPosition="right"
          name="address2"
          type="text"
          error={Boolean(touched.address2 && errors.address2)}
          value={values.address2}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field className="ButtonHolder">
        <Button type="submit" primary fluid>
          Sign up
        </Button>
      </Form.Field>
    </Form>
  );
};

export default SignUp;
