import React from 'react';
import { Table } from 'semantic-ui-react';

import { formatCurrency } from '../../utils/numbers';

import './StatementsTable.scss';

import type { StatementDTO } from '../../api';

type Props = {
  list: StatementDTO[];
};

const StatementsTable: React.FC<Props> = ({ list }) => (
  <Table className="StatementsTable">
    <Table.Header>
      <Table.Row>
        <Table.HeaderCell rowSpan="2">Buyer name</Table.HeaderCell>
        <Table.HeaderCell className="Centered" colSpan="2">
          Last period
        </Table.HeaderCell>
        <Table.HeaderCell className="Centered" colSpan="2">
          Current period
        </Table.HeaderCell>
      </Table.Row>
      <Table.Row>
        <Table.HeaderCell className="Description">Total spend</Table.HeaderCell>
        <Table.HeaderCell className="Description">Cards</Table.HeaderCell>
        <Table.HeaderCell className="Description">Total spend</Table.HeaderCell>
        <Table.HeaderCell className="Description">Cards</Table.HeaderCell>
      </Table.Row>
    </Table.Header>
    <Table.Body>
      {list.map((statement) => (
        <Table.Row key={statement.id}>
          <Table.Cell>{statement.buyer}</Table.Cell>
          <Table.Cell>{formatCurrency(statement.lastPeriodTotal)}</Table.Cell>
          <Table.Cell>{statement.lastPeriodCards}</Table.Cell>
          <Table.Cell>
            {formatCurrency(statement.currentPeriodTotal)}
          </Table.Cell>
          <Table.Cell>{statement.currentPeriodCards}</Table.Cell>
        </Table.Row>
      ))}
    </Table.Body>
  </Table>
);

export default StatementsTable;
