import React from 'react';
import { Button, Form, Header, Input } from 'semantic-ui-react';

import { ReactComponent as MainIcon } from './images/mainIcon.svg';

type Props = {
  handleForgotPasswordClick: () => void;
  handleNextStepClick: () => void;
};

const TypePassword: React.FC<Props> = ({
  handleForgotPasswordClick,
  handleNextStepClick,
}) => (
  <section className="TypePassword">
    <MainIcon />
    <Header as="h2">Enter your password</Header>
    <p>This is security check to keep your account safe</p>
    <Form>
      <Form.Field>
        <Input type="password" label="Password" labelPosition="right" />
      </Form.Field>
      <Form.Field>
        <Button primary onClick={handleNextStepClick}>
          Continue
        </Button>
      </Form.Field>
    </Form>
    <p className="Additional">
      <span role="presentation" onClick={handleForgotPasswordClick}>
        Forgot your password?
      </span>
    </p>
  </section>
);

export default TypePassword;
