import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { getAllCards } from '../../api';
import { fetchCardsList } from '../../common/cards/actions';
import { cardsListSelector } from '../../common/cards/selectors';

import type { CardDTO } from '../../api';

type ReturnType = {
  bootstrap: () => void;
  list: CardDTO[];
};

const useBootstrap = (): ReturnType => {
  const dispatch = useDispatch();

  const list = useSelector(cardsListSelector);

  const { run: handleFetchCards } = useRequest<CardDTO[]>(getAllCards, {
    manual: true,
  });

  const bootstrap = useCallback(async () => {
    const data = await handleFetchCards();

    if (data) {
      dispatch(fetchCardsList(data));
    } else {
      toast.error('Fetch cards list error');
    }
  }, [dispatch]);

  return { bootstrap, list };
};

export default useBootstrap;
