import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { Button, Checkbox, Form, Input } from 'semantic-ui-react';
import { toast } from 'react-toastify';

import { POSITION_OPTIONS } from '../../constants';

import type { FullRegisterDTO } from '../../api';
import type { RegistrationSteps } from '../../types';

type Props = {
  setFormValues: (data: FullRegisterDTO) => void;
  setStep: (step: RegistrationSteps) => void;
};

const SignUp1: React.FC<Props> = ({ setFormValues, setStep }) => {
  const [agree, setAgree] = useState(false);

  const initialValues = {
    businessName: '',
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    position: '',
  };

  const { errors, touched, values, handleChange, handleSubmit } = useFormik({
    initialValues,
    validationSchema: yup.object({
      businessName: yup.string().min(3).required('Enter business name'),
      firstName: yup.string().min(3).required('Enter first name'),
      lastName: yup.string().min(3).required('Enter last name'),
      email: yup.string().email().required('Enter email'),
      password: yup
        .string()
        .min(8, 'Password must contain at least 8 symbols')
        .max(32, 'Password maximum length  32 symbols')
        .matches(
          /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])/,
          'Password must contain at least one uppercase, one number and one special symbol',
        )
        .required('Enter password'),
      position: yup.string().min(3).required(),
    }),
    onSubmit: (data) => {
      if (agree) {
        setFormValues(data);
        setStep(2);
      } else {
        toast.error('You must check agreement');
      }
    },
  });

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Field>
        <Input
          className={values.businessName ? 'filled' : ''}
          label="Business name"
          labelPosition="right"
          name="businessName"
          type="text"
          error={Boolean(touched.businessName && errors.businessName)}
          value={values.businessName}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.firstName ? 'filled' : ''}
          label="First name"
          labelPosition="right"
          name="firstName"
          type="text"
          error={Boolean(touched.firstName && errors.firstName)}
          value={values.firstName}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.lastName ? 'filled' : ''}
          label="Last name"
          labelPosition="right"
          name="lastName"
          type="text"
          error={Boolean(touched.lastName && errors.lastName)}
          value={values.lastName}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.email ? 'filled' : ''}
          label="Email"
          labelPosition="right"
          name="email"
          type="email"
          error={Boolean(touched.email && errors.email)}
          value={values.email}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.password ? 'filled' : ''}
          label="Password"
          labelPosition="right"
          name="password"
          type="password"
          error={Boolean(touched.password && errors.password)}
          value={values.password}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <select
          className={touched.position && errors.position ? 'error' : ''}
          name="position"
          placeholder="Position"
          defaultValue=""
          onChange={handleChange}
        >
          <option value="" disabled>
            Position
          </option>
          {POSITION_OPTIONS.map((option) => (
            <option key={option.key} value={option.value}>
              {option.text}
            </option>
          ))}
        </select>
      </Form.Field>
      <Form.Field>
        <Checkbox
          label="Agree with terms and conditions"
          defaultChecked={agree}
          onChange={() => setAgree(!agree)}
        />
      </Form.Field>
      <Form.Field className="ButtonHolder">
        <Button type="submit" primary fluid>
          Register
        </Button>
      </Form.Field>
    </Form>
  );
};

export default SignUp1;
