import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { getAllUsers, getUserData } from '../../api';

import {
  fetchUserData,
  fetchUsersList,
  signInAction,
} from '../../common/users/actions';
import {
  isUserAdminSelector,
  isUserHaveAdditionalRights,
  userDataSelector,
} from '../../common/users/selectors';

import { deleteTokens } from '../../utils/tokens';

import type { UserDTO } from '../../api';

type ReturnType = {
  admin: boolean;
  adminLike: boolean;
  bootstrap: () => void;
  signOut: () => void;
  user: UserDTO;
};

const useBootstrap = (): ReturnType => {
  const dispatch = useDispatch();

  const user = useSelector(userDataSelector);
  const admin = useSelector(isUserAdminSelector);
  const adminLike = useSelector(isUserHaveAdditionalRights);

  const { run: fetchUser } = useRequest<UserDTO>(getUserData, {
    manual: true,
  });

  const { run: fetchAllUsers } = useRequest<UserDTO[]>(getAllUsers, {
    manual: true,
  });

  const bootstrap = useCallback(async () => {
    const data = await Promise.all([fetchUser(), fetchAllUsers()]);

    if (data.length) {
      dispatch(fetchUserData(data[0]));
      dispatch(fetchUsersList(data[1]));
    } else {
      toast.error('Fetch users data error');
    }
  }, [dispatch]);

  const signOut = useCallback(() => {
    deleteTokens();
    dispatch(signInAction(false));
  }, [dispatch]);

  return {
    admin,
    adminLike,
    bootstrap,
    signOut,
    user,
  };
};

export default useBootstrap;
