import React from 'react';
import { Input, Radio, Form as SUIForm } from 'semantic-ui-react';

import useForm from './useForm';
import './Form.scss';

const Form: React.FC = () => {
  const { onChange, values } = useForm();

  return (
    <SUIForm className="SettingsForm">
      <div>
        <SUIForm.Field>
          <label htmlFor="businessName">Business name</label>
          <Input
            id="businessName"
            name="businessName"
            type="text"
            value={values.businessName}
            onChange={({ target: { value } }) =>
              onChange({ key: 'businessName', value })
            }
          />
        </SUIForm.Field>
        <SUIForm.Field>
          <label htmlFor="name">Name</label>
          <Input
            id="name"
            name="name"
            type="text"
            value={values.name}
            onChange={({ target: { value } }) =>
              onChange({ key: 'name', value })
            }
          />
        </SUIForm.Field>
        <SUIForm.Field>
          <label htmlFor="email">Email</label>
          <Input
            id="email"
            name="email"
            type="email"
            value={values.email}
            onChange={({ target: { value } }) =>
              onChange({ key: 'email', value })
            }
          />
        </SUIForm.Field>
        <SUIForm.Field>
          <label htmlFor="phone">Phone number</label>
          <Input
            id="phone"
            name="phone"
            type="text"
            value={values.phone}
            onChange={({ target: { value } }) =>
              onChange({ key: 'phone', value })
            }
          />
        </SUIForm.Field>
      </div>
      <div>
        <SUIForm.Field>
          <label>Email notification</label>
          <div className="ToggleText">
            <span>Receive daily email notification any time.</span>
            <Radio
              name="emailNotification"
              defaultChecked={values.emailNotification}
              toggle
              onChange={() =>
                onChange({
                  key: 'emailNotification',
                  value: Boolean(!values.emailNotification),
                })
              }
            />
          </div>
        </SUIForm.Field>
        <SUIForm.Field>
          <label>Telegram notification</label>
          <div className="ToggleText">
            <span>Receive daily telegram notification any time.</span>
            <Radio
              name="telegramNotification"
              defaultChecked={values.telegramNotification}
              toggle
              onChange={() =>
                onChange({
                  key: 'telegramNotification',
                  value: Boolean(!values.telegramNotification),
                })
              }
            />
          </div>
        </SUIForm.Field>
      </div>
    </SUIForm>
  );
};

export default Form;
