import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { EditUserType, UserDTO, editUserById } from '../../api';

import useBootstrap from '../Users/useBootstrap';

type ReturnType = {
  editUser: (data: EditUserType) => void;
};

const useEditUser = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const { run: handleEditUser } = useRequest<UserDTO>(editUserById, {
    manual: true,
  });

  const editUser = useCallback(async (id) => {
    const response = await handleEditUser(id);

    if (response.id) {
      toast.success('User edited successfully');
      bootstrap();
    } else {
      toast.error('Cannot edit user');
    }
  }, []);

  return {
    editUser,
  };
};

export default useEditUser;
