import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { useRequest } from '@umijs/hooks';
import qs from 'query-string';
import { debounce } from 'lodash';

import { getAllCards } from '../../api';
import type { CardDTO } from '../../api';

type ReturnType = {
  handleChange: (query: string) => void;
};

const useSearch = (): ReturnType => {
  const history = useHistory();

  const { run: handleFetchCards } = useRequest<CardDTO[]>(getAllCards, {
    manual: true,
  });

  const handleChange = useCallback(
    debounce(async (query) => {
      history.push({ search: query ? qs.stringify({ q: query }) : '' });

      // Here must be bootstrap
      const response = await handleFetchCards(query ? { q: query } : undefined);

      console.log(response);
    }, 500),
    [],
  );

  return {
    handleChange,
  };
};

export default useSearch;
