import React from 'react';
import cn from 'classnames';
import { BsExclamationCircle, BsExclamationTriangle } from 'react-icons/bs';
import { FaCog, FaRegCheckCircle, FaRegTimesCircle } from 'react-icons/fa';

import type { Badge as BadgeType } from './types';

import './Badge.scss';

type Props = {
  type?: BadgeType;
};

const Badge: React.FC<Props> = ({ type = 'success' }) => {
  const classes = cn('Badge', type);

  switch (type) {
    case 'default':
      return <BsExclamationCircle className={classes} />;

    case 'success':
      return <FaRegCheckCircle className={classes} />;

    case 'error':
      return <FaRegTimesCircle className={classes} />;

    case 'warning':
      return <BsExclamationTriangle className={classes} />;

    case 'settings':
      return <FaCog className={classes} />;

    default:
      return <FaRegCheckCircle className={classes} />;
  }
};

export default Badge;
