import * as constants from './constants';

import type { SignUpType, UserDTO } from '../../api';
import type * as types from './types';

export const signUpAction = (data: SignUpType): types.SignUpActionType => ({
  type: constants.SIGN_UP,
  payload: data,
});

export const signInAction = (isSigned: boolean): types.IsSignedActionType => ({
  type: constants.IS_SIGNED_IN,
  payload: isSigned,
});

export const fetchUsersList = (data: UserDTO[]): types.FetchUsersList => ({
  type: constants.FETCH_USERS_LIST,
  payload: data,
});

export const fetchUserData = (data: UserDTO): types.FetchUserData => ({
  type: constants.FETCH_USER_DATA,
  payload: data,
});
