import * as constants from './constants';

import type { UsersActionTypes, UsersState } from './types';
import type { UserDTO } from '../../api';

const initialState: UsersState = {
  isLogged: false,
  list: [],
  user: {} as UserDTO,
};

const reducer = (
  state = initialState,
  action: UsersActionTypes,
): UsersState => {
  switch (action.type) {
    case constants.IS_SIGNED_IN:
      return {
        ...state,
        isLogged: action.payload,
      };

    case constants.FETCH_USERS_LIST:
      return {
        ...state,
        list: action.payload,
      };

    case constants.FETCH_USER_DATA:
      return {
        ...state,
        user: action.payload,
      };

    default:
      return state;
  }
};

export default reducer;
