import * as constants from './constants';

import type {
  ChangeThemeActionType,
  SetLoadingActionType,
  Themes,
} from './types';

export const changeThemeAction = (theme: Themes): ChangeThemeActionType => ({
  type: constants.CHANGE_THEME,
  payload: theme,
});

export const setLoadingAction = (isLoading: boolean): SetLoadingActionType => ({
  type: constants.SET_LOADING,
  payload: isLoading,
});
