import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { getAllStatements } from '../../api';
import type { StatementDTO } from '../../api';

type ReturnType = {
  bootstrap: () => void;
  list: StatementDTO[];
};

const useBootstrap = (): ReturnType => {
  const list = [
    {
      id: 1,
      buyer: 'Ilya Savitsky',
      lastPeriodTotal: 1342.08,
      lastPeriodCards: 400,
      currentPeriodTotal: 1342.08,
      currentPeriodCards: 400,
    },
    {
      id: 2,
      buyer: 'Ilya Savitsky',
      lastPeriodTotal: 1342.08,
      lastPeriodCards: 400,
      currentPeriodTotal: 1342.08,
      currentPeriodCards: 400,
    },
    {
      id: 3,
      buyer: 'Ilya Savitsky',
      lastPeriodTotal: 1342.08,
      lastPeriodCards: 400,
      currentPeriodTotal: 1342.08,
      currentPeriodCards: 400,
    },
  ] as StatementDTO[];

  const { run: handleFetchStatements } = useRequest<StatementDTO[]>(
    getAllStatements,
    {
      manual: true,
    },
  );

  const bootstrap = useCallback(async () => {
    const data = await handleFetchStatements();

    if (data) {
      console.log('Success');
    } else {
      toast.error('Statements fetch error');
    }
  }, []);

  return { bootstrap, list };
};

export default useBootstrap;
