import React from 'react';
import { Link } from 'react-router-dom';
import { useFormik } from 'formik';
import { BsEyeSlash } from 'react-icons/bs';
import { Button, Form, Header, Input, Segment } from 'semantic-ui-react';

import useSign from './useSign';
import './Sign.scss';

const SignIn: React.FC = () => {
  const { handleSignInSubmit, inputType, setType } = useSign();

  const initialValues = {
    email: '',
    password: '',
  };

  const { errors, touched, values, handleChange, handleSubmit } = useFormik({
    initialValues,
    onSubmit: (data) => handleSignInSubmit(data),
  });

  return (
    <Segment basic>
      <div />
      <section className="Form">
        <Header as="h1">Sign In</Header>
        <Form onSubmit={handleSubmit}>
          <Form.Field>
            <Input
              className={values.email ? 'filled' : ''}
              label="Email"
              labelPosition="right"
              name="email"
              type="email"
              error={Boolean(touched.email && errors.email)}
              value={values.email}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <Input
              className={values.password ? 'filled' : ''}
              label="Password"
              labelPosition="right"
              name="password"
              type={inputType}
              error={Boolean(touched.password && errors.password)}
              icon={
                <BsEyeSlash
                  onMouseDown={() => setType('text')}
                  onMouseUp={() => setType('password')}
                />
              }
              value={values.password}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field className="ButtonHolder">
            <Button primary fluid>
              Sign in
            </Button>
          </Form.Field>
        </Form>
        <p className="Forget">
          <Link to="/password-restore">Forgot your password?</Link>
        </p>
      </section>
      <section>
        Don&apos;t have an account? <Link to="/sign-up">Sign up</Link>
      </section>
    </Segment>
  );
};

export default SignIn;
