import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRequest } from '@umijs/hooks';

import { getAllUsers } from '../../api';
import type { UserDTO } from '../../api';

import { fetchUsersList } from '../../common/users/actions';
import { usersListSelector } from '../../common/users/selectors';

type ReturnType = {
  bootstrap: () => void;
  list: UserDTO[];
};

const useBootstrap = (): ReturnType => {
  const dispatch = useDispatch();

  const list = useSelector(usersListSelector);

  const { run: fetchAllUsers } = useRequest<UserDTO[]>(getAllUsers, {
    manual: true,
  });

  const bootstrap = useCallback(async () => {
    if (!list.length) {
      const data = await fetchAllUsers();

      if (data.length) {
        dispatch(fetchUsersList(data));
      }
    }
  }, [dispatch]);

  return {
    bootstrap,
    list,
  };
};

export default useBootstrap;
