import React from 'react';
import moment from 'moment';
import { FaAngleDown } from 'react-icons/fa';
import { Dropdown, Table } from 'semantic-ui-react';

import { Status } from '../../components';

import EditUserModal from '../EditUserModal/EditUserModal';

import { EDITABLE_USER_ROLES, USER_ROLES } from '../../api';

import { toCamelCase } from '../../utils/strings';

import type {
  EditUserType,
  SetUserRoleType,
  UserDTO,
  UserRoles,
} from '../../api';

type Props = {
  user: UserDTO;
  handleBlockUser: (id: number) => void;
  handleUnblockUser: (id: number) => void;
  handleSetUserRole: (data: SetUserRoleType) => void;
  handleEditUser: (data: EditUserType) => void;
};

const UsersListRow: React.FC<Props> = ({
  user,
  handleBlockUser,
  handleUnblockUser,
  handleSetUserRole,
  handleEditUser,
}) => (
  <Table.Row>
    <Table.Cell>{user.id}</Table.Cell>
    <Table.Cell>
      {user.firstName} {user.lastName}
    </Table.Cell>
    <Table.Cell>{moment(user.createDate).format('DD/MM/YYYY')}</Table.Cell>
    <Table.Cell>{user.email}</Table.Cell>
    <Table.Cell>{user.expectedVolume}</Table.Cell>
    <Table.Cell>
      {user.role === toCamelCase(USER_ROLES[1]) ? (
        USER_ROLES[1]
      ) : (
        <Dropdown
          icon={<FaAngleDown />}
          defaultValue={user.role}
          options={EDITABLE_USER_ROLES.map((role) => ({
            key: role,
            text: role,
            value: toCamelCase(role),
            disabled: user.role === toCamelCase(role),
          }))}
          onChange={(_, { value }) =>
            handleSetUserRole({ id: user.id, role: value as UserRoles })
          }
        />
      )}
    </Table.Cell>
    <Table.Cell>
      <EditUserModal user={user} handleEditUser={handleEditUser} />
    </Table.Cell>
    <Table.Cell>
      {user.userState === 'Blocked' ? (
        <span
          className="BlockAction"
          role="presentation"
          onClick={() => handleUnblockUser(user.id)}
        >
          Unblock
        </span>
      ) : (
        <span
          className="BlockAction Block"
          role="presentation"
          onClick={() => handleBlockUser(user.id)}
        >
          Block
        </span>
      )}
    </Table.Cell>
    <Table.Cell>
      <Status type={user.userState} />
    </Table.Cell>
  </Table.Row>
);

export default UsersListRow;
