import { useSelector } from 'react-redux';

import {
  isUserAdminSelector,
  isUserLeadSelector,
  isUserRootSelector,
  isUserTrustedSelector,
} from '../../common/users/selectors';

type ReturnType = {
  adminLike: boolean;
  notBuyer: boolean;
};

const useRoles = (): ReturnType => {
  const isAdmin = useSelector(isUserAdminSelector);
  const isLead = useSelector(isUserLeadSelector);
  const isRoot = useSelector(isUserRootSelector);
  const isTrusted = useSelector(isUserTrustedSelector);

  const notBuyer = isAdmin || isLead || isRoot || isTrusted;
  const adminLike = isAdmin || isLead || isRoot;

  return { adminLike, notBuyer };
};

export default useRoles;
