import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { UserDTO, blockUserById, unblockUserById } from '../../api';

import useBootstrap from '../Users/useBootstrap';

type ReturnType = {
  blockUser: (id: number) => void;
  unblockUser: (id: number) => void;
};

const useBlocking = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const { run: handleBlockUser } = useRequest<UserDTO>(blockUserById, {
    manual: true,
  });
  const { run: handleUnblockUser } = useRequest<UserDTO>(unblockUserById, {
    manual: true,
  });

  const blockUser = useCallback(async (id) => {
    const response = await handleBlockUser(id);

    if (response.id) {
      toast.success('User blocked successfully');
      bootstrap();
    } else {
      toast.error('Cannot block user');
    }
  }, []);

  const unblockUser = useCallback(async (id) => {
    const response = await handleUnblockUser(id);

    if (response.id) {
      toast.success('User unblocked successfully');
      bootstrap();
    } else {
      toast.error('Cannot unblock user');
    }
  }, []);

  return {
    blockUser,
    unblockUser,
  };
};

export default useBlocking;
