import React from 'react';
import moment from 'moment';
import { Link } from 'react-router-dom';
import {
  FaAngleDown,
  FaSignOutAlt,
  FaTimesCircle,
  FaUser,
} from 'react-icons/fa';
import { BsBellFill, BsQuestionCircleFill } from 'react-icons/bs';
import { Button, Dropdown, Modal, Popup } from 'semantic-ui-react';

import { Badge } from '../../components';

import Notifications from '../Notifications/Notifications';

import useBootstrap from './useBootstrap';
import useNotifications from './useNotifications';
import './Header.scss';

const Header: React.FC = () => {
  const { signOut, user } = useBootstrap();
  const { handleCloseModal, handleOpenModal, list, notificationsModal } =
    useNotifications();

  return (
    <header className="Header">
      <div className="Actions">
        <Button as={Link} to="/faq" content={<BsQuestionCircleFill />} basic />
        <Popup
          trigger={
            <span className="BellHolder new">
              <Button content={<BsBellFill />} />
            </span>
          }
          content={<Notifications onOpen={handleOpenModal} />}
          on="click"
          position="bottom center"
        />
        <Dropdown
          icon={<FaAngleDown />}
          text={user.firstName ? `${user.firstName} ${user.lastName}` : ''}
        >
          <Dropdown.Menu>
            <Dropdown.Item
              icon={<FaUser />}
              text="Profile"
              as={Link}
              to="/settings"
            />
            <Dropdown.Item
              icon={<FaSignOutAlt />}
              text="Exit"
              onClick={signOut}
            />
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <Modal
        className="NotificationsModal"
        size="tiny"
        open={notificationsModal}
        closeIcon={<FaTimesCircle />}
        onClose={handleCloseModal}
      >
        <Modal.Header>Notifications</Modal.Header>
        <Modal.Content>
          <ul>
            {list.map((item) => (
              <li key={item.date}>
                <Badge type={item.type} />
                <p>{item.text}</p>
                <p className="Date">{moment(item.date).format('DD-MM-YYYY')}</p>
              </li>
            ))}
          </ul>
        </Modal.Content>
      </Modal>
    </header>
  );
};

export default Header;
