import React from 'react';
import { BsBoxArrowInDown, BsCalendar } from 'react-icons/bs';
import { FaAngleDown } from 'react-icons/fa';
import { Button, Dropdown, Popup } from 'semantic-ui-react';

import { Calendar, Status } from '../../components';

import useExport from './useExport';
import useFilters from './useFilters';
import { ReactComponent as CsvIcon } from './images/csv.svg';
import { ReactComponent as PdfIcon } from './images/pdf.svg';
import { ReactComponent as XlsIcon } from './images/xls.svg';
import './StatementsHeader.scss';

const StatementsHeader: React.FC = () => {
  const { handleExportStatement } = useExport();
  const {
    handleFilterByBuyer,
    handleFilterByDate,
    handleFilterByStatus,
    buyerFilter,
    statusFilter,
    usersList,
    endDate,
    startDate,
    setEndDate,
    setStartDate,
  } = useFilters();

  return (
    <div className="StatementsHeader">
      <div>
        <p>Status</p>
        <Dropdown icon={<FaAngleDown />} text={statusFilter || 'All'}>
          <Dropdown.Menu>
            <Dropdown.Item onClick={() => handleFilterByStatus(undefined)}>
              All statuses
            </Dropdown.Item>
            <Dropdown.Item onClick={() => handleFilterByStatus('active')}>
              <Status type="active" />
            </Dropdown.Item>
            <Dropdown.Item onClick={() => handleFilterByStatus('paused')}>
              <Status type="paused" />
            </Dropdown.Item>
            <Dropdown.Item onClick={() => handleFilterByStatus('blocked')}>
              <Status type="blocked" />
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div>
        <p>Buyer</p>
        <Dropdown icon={<FaAngleDown />} text={buyerFilter || 'All'}>
          <Dropdown.Menu>
            <Dropdown.Item onClick={() => handleFilterByBuyer(undefined)}>
              All users
            </Dropdown.Item>
            {usersList.map((user) => (
              <Dropdown.Item
                onClick={() =>
                  handleFilterByBuyer(
                    user.id,
                    `${user.firstName} ${user.lastName}`,
                  )
                }
              >
                {user.firstName} {user.lastName}
              </Dropdown.Item>
            ))}
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div>
        <p>Date</p>
        <Dropdown icon={<FaAngleDown />} text="Last month">
          <Dropdown.Menu>
            <Dropdown.Item onClick={() => handleFilterByDate(undefined)}>
              Last month
            </Dropdown.Item>
            <Dropdown.Item onClick={() => handleFilterByDate('')}>
              This month
            </Dropdown.Item>
            <Popup
              className="CalendarPopup"
              trigger={
                <Dropdown.Item>
                  <BsCalendar />
                  Custom
                </Dropdown.Item>
              }
              content={
                <Calendar
                  endDate={endDate}
                  startDate={startDate}
                  setEndDate={setEndDate}
                  setStartDate={setStartDate}
                />
              }
              position="right center"
              hoverable
            />
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div>
        <Dropdown
          className="Export"
          trigger={
            <Button primary>
              <BsBoxArrowInDown /> Export
            </Button>
          }
        >
          <Dropdown.Menu>
            <Dropdown.Item
              icon={<XlsIcon />}
              text="XLSX"
              onClick={() => handleExportStatement('xlsx')}
            />
            <Dropdown.Item
              icon={<PdfIcon />}
              text="PDF"
              onClick={() => handleExportStatement('pdf')}
            />
            <Dropdown.Item
              icon={<CsvIcon />}
              text="CSV"
              onClick={() => handleExportStatement('csv')}
            />
          </Dropdown.Menu>
        </Dropdown>
      </div>
    </div>
  );
};

export default StatementsHeader;
