import React, { useState } from 'react';
import { Button, Modal } from 'semantic-ui-react';

import Balance from './Balance';
import TopUpsList from '../TopUpsList/TopUpsList';

import { BarChart, Card } from '../../components';
import { formatCurrency } from '../../utils/numbers';

import useBootstrap from './useBootstrap';
import useRoles from './useRoles';
import './Dashboard.scss';

const Dashboard: React.FC = () => {
  const [topUpModal, setTopUpModal] = useState(false);
  const {
    activeCards,
    averageCardsLifetime,
    averageDaySpend,
    cards,
    cp,
    cardsOnPause,
    moneyBalance,
    topUpsList,
  } = useBootstrap();
  const { adminLike } = useRoles();

  return (
    <section className="Dashboard">
      {adminLike && (
        <Balance cards={cards} cp={cp} moneyBalance={moneyBalance} />
      )}
      <div className="Wrapper">
        <div className="Chart">
          <div className="ChartHolder">
            <header>
              <h3>Spendings</h3>
              <Button.Group>
                <Button className="active">Last 14 d.</Button>
                <Button>Last year</Button>
              </Button.Group>
            </header>
            <BarChart />
          </div>
        </div>
        <div className="Holder">
          <Card
            title="Active cards"
            titleSize="big"
            link="/cards?status=active"
            linkText="More detailed"
            isPadded
          >
            <span className="Number">{activeCards}</span>
          </Card>
          <Card
            title="Av. cards lifetime"
            titleDescription="last 7 d."
            link="/"
            linkText="Switch"
            isPadded
          >
            <div className="Days">
              <div>{averageCardsLifetime}</div>
              <div>
                <span>days</span>
              </div>
            </div>
          </Card>
        </div>
        <div className="Holder">
          <Card
            title="Cards on pause"
            titleSize="big"
            link="/cards?status=paused"
            linkText="More detailed"
            isPadded
          >
            <span className="Number">{cardsOnPause}</span>
          </Card>

          <Card
            className="Avg"
            title="Av. day spend"
            titleDescription="last 7 d."
            link="/"
            linkText="Switch"
            isPadded
          >
            <span className="Number thin">
              {formatCurrency(averageDaySpend)}
            </span>
            <i className="Enough">enough for 7 days</i>
          </Card>
        </div>
      </div>
      {adminLike && (
        <div className="Wrapper">
          <div className="Holder Pie">
            <Card title="Usage limit" isPadded>
              Chart
            </Card>
          </div>
          <div className="List">
            <Card>
              <TopUpsList items={topUpsList} title="Last Top-ups" />
            </Card>
          </div>
        </div>
      )}

      <Modal
        size="small"
        open={topUpModal}
        onClose={() => setTopUpModal(false)}
      >
        <Modal.Header>Top up options</Modal.Header>
        <Modal.Content>Content</Modal.Content>
        <Modal.Actions>
          <Button basic onClick={() => setTopUpModal(false)}>
            Cancel
          </Button>
          <Button primary>Continue</Button>
        </Modal.Actions>
      </Modal>
    </section>
  );
};

export default Dashboard;
