import React from 'react';
import cn from 'classnames';
import { ToastContainer } from 'react-toastify';
import { Segment } from 'semantic-ui-react';

import Header from './Header';
import Sidebar from './Sidebar';

import { Themes } from '../../common/app/types';

import './Layout.scss';

type Props = {
  appName: string;
  appTheme: Themes;
  children: React.ReactNode;
};

const Layout: React.FC<Props> = ({ appName, appTheme, children }) => {
  const classes = cn('Layout', appTheme);

  return (
    <Segment className={classes} basic>
      <section className="Wrapper">
        <Sidebar appName={appName} />
        <main>
          <Header />
          <div className="Holder">{children}</div>
        </main>
      </section>
      <ToastContainer
        position="top-right"
        autoClose={3000}
        rtl={false}
        hideProgressBar
        newestOnTop
        closeOnClick
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </Segment>
  );
};

export default Layout;
