import React from 'react';
import cn from 'classnames';

import { CardStatuses, UserStatus } from '../../api';

import './Status.scss';

type Props = {
  type?: CardStatuses | UserStatus;
};

const Status: React.FC<Props> = ({ type = 'active' }) => {
  const classes = cn('Status', type.toLowerCase());

  return (
    <span className={classes}>
      <i />
      {type}
    </span>
  );
};

export default Status;
