import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { setLoadingAction } from '../../common/app/actions';
import {
  fetchUserData,
  fetchUsersList,
  signInAction,
} from '../../common/users/actions';

import {
  appIsLoadingSelector,
  appNameSelector,
  appThemeSelector,
} from '../../common/app/selectors';
import {
  isLoggedSelector,
  userDataSelector,
} from '../../common/users/selectors';

import { checkLsAccessToken } from '../../utils/tokens';

import { getAllUsers, getUserData } from '../../api';

import type { Themes } from '../../common/app/types';
import type { UserDTO } from '../../api';

type ReturnType = {
  appName: string;
  appTheme: Themes;
  bootstrap: () => void;
  checkToken: () => void;
  isLoading: boolean;
  isLogged: boolean;
  user: UserDTO;
};

const useApp = (): ReturnType => {
  const dispatch = useDispatch();

  const appName = useSelector(appNameSelector);
  const appTheme = useSelector(appThemeSelector);
  const isLoading = useSelector(appIsLoadingSelector);
  const isLogged = useSelector(isLoggedSelector);
  const user = useSelector(userDataSelector);

  const checkToken = useCallback(() => {
    dispatch(signInAction(checkLsAccessToken()));
  }, [dispatch]);

  const { run: fetchUser } = useRequest<UserDTO>(getUserData, {
    manual: true,
  });

  const { run: fetchAllUsers } = useRequest<UserDTO[]>(getAllUsers, {
    manual: true,
  });

  const bootstrap = useCallback(async () => {
    const data = await Promise.all([fetchUser(), fetchAllUsers()]);

    if (data.length) {
      dispatch(setLoadingAction(false));
      dispatch(fetchUserData(data[0]));
      dispatch(fetchUsersList(data[1]));
    } else {
      toast.error('Fetch users data error');
    }
  }, [dispatch]);

  return {
    appName,
    appTheme,
    bootstrap,
    checkToken,
    isLoading,
    isLogged,
    user,
  };
};

export default useApp;
