import React, { useEffect } from 'react';
import { Redirect, Route, Switch } from 'react-router';

import Analytics from '../Analytics/Analytics';
import Balances from '../Balances/Balances';
import Cards from '../Cards/Cards';
import Dashboard from '../Dashboard/Dashboard';
import EmailConfirmation from '../Sign/EmailConfirmation';
import FAQ from '../FAQ/FAQ';
import Layout from '../Layout/Layout';
import Settings from '../Settings/Settings';
import Sign from '../Sign/Sign';
import SignIn from '../Sign/SignIn';
import SignUp from '../Sign/SignUp';
import PasswordRestore from '../Sign/PasswordRestore';
import PhoneConfirmation from '../Sign/PhoneConfirmation';
import Users from '../Users/Users';

import { Loading } from '../../components';

import useApp from './useApp';

const App: React.FC = () => {
  const {
    appName,
    appTheme,
    bootstrap,
    checkToken,
    isLoading,
    isLogged,
    user,
  } = useApp();

  useEffect(() => checkToken(), []);

  useEffect(() => {
    if (isLogged) {
      bootstrap();
    }
  }, [isLogged]);

  if (!isLogged) {
    return (
      <Sign appName={appName} appTheme={appTheme}>
        <Switch>
          <Route path="/sign-in" component={SignIn} exact />
          <Route path="/sign-up" component={SignUp} exact />
          <Route path="/phone-confirm" component={PhoneConfirmation} exact />
          <Route path="/email-confirm" component={EmailConfirmation} exact />
          <Route path="/password-restore" component={PasswordRestore} exact />
          <Redirect to="/sign-in" />
        </Switch>
      </Sign>
    );
  }

  if (isLoading || !user) {
    return <Loading />;
  }

  if (!isLoading && !user.emailConfirmed) {
    return (
      <Sign appName={appName} appTheme={appTheme}>
        <EmailConfirmation />
      </Sign>
    );
  }

  if (!isLoading && !user.phoneConfirmed) {
    return (
      <Sign appName={appName} appTheme={appTheme}>
        <PhoneConfirmation />
      </Sign>
    );
  }

  return (
    <Layout appName={appName} appTheme={appTheme}>
      <Switch>
        <Route path="/" component={Dashboard} exact />
        <Route path="/cards" component={Cards} exact />
        <Route path="/balances" component={Balances} exact />
        <Route path="/analytics" component={Analytics} exact />
        <Route path="/faq" component={FAQ} exact />
        <Route path="/users-list" component={Users} exact />
        <Route path="/settings" component={Settings} exact />
        <Redirect to="/" />
      </Switch>
    </Layout>
  );
};

export default App;
