import React, { useEffect } from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { Button, Input, Radio, Form as SUIForm } from 'semantic-ui-react';

import useForm from './useForm';
import './Form.scss';

import type { CreateCardType } from '../../api';

type Props = {
  handleCreateNewCard: (data: CreateCardType) => void;
};

const Form: React.FC<Props> = ({ handleCreateNewCard }) => {
  const { getUsersList, usersList } = useForm();
  const initialValues = {
    type: 'virtual',
    mode: 'debit',
    balance: 'usd',
    codes: 'fbGoogle',
    dailyLimit: undefined,
    monthLimit: undefined,
    quantity: undefined,
    buyer: undefined,
  } as CreateCardType;

  const { errors, values, handleChange, handleSubmit } = useFormik({
    initialValues,
    validationSchema: yup.object({
      dailyLimit: yup.number().required(),
      monthLimit: yup.number().required(),
      buyer: yup.number(),
    }),
    onSubmit: handleCreateNewCard,
  });

  useEffect(() => {
    if (!usersList.length) {
      getUsersList();
    }
  }, [getUsersList, usersList]);

  return (
    <SUIForm className="AddCardForm" onSubmit={handleSubmit}>
      <SUIForm.Field inline>
        <label>Card type</label>
        <div>
          <Radio
            name="type"
            value="virtual"
            label="Virtual"
            checked={values.type === 'virtual'}
            onChange={(_, target) => handleChange({ target })}
          />
          <Radio
            name="type"
            value="physical"
            label="Physical"
            checked={values.type === 'physical'}
            onChange={(_, target) => handleChange({ target })}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Card mode</label>
        <div>
          <Radio
            name="mode"
            value="debit"
            label="Debit"
            checked={values.mode === 'debit'}
            onChange={(_, target) => handleChange({ target })}
          />
          <Radio
            name="mode"
            value="prepaid"
            label="Prepaid"
            checked={values.mode === 'prepaid'}
            onChange={(_, target) => handleChange({ target })}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Card balance</label>
        <div>
          <select
            name="balance"
            defaultValue={values.balance}
            onBlur={handleChange}
          >
            <option value="usd">USD</option>
            <option value="eur">EUR</option>
            <option value="rub">RUB</option>
          </select>
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>MCC codes</label>
        <div>
          <Radio
            name="codes"
            value="fbGoogle"
            label="FB / Google"
            checked={values.codes === 'fbGoogle'}
            onChange={(_, target) => handleChange({ target })}
          />
          <Radio
            name="codes"
            value="any"
            label="Any"
            checked={values.codes === 'any'}
            onChange={(_, target) => handleChange({ target })}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Daily limit</label>
        <div>
          <Input
            className={errors.dailyLimit ? 'error' : ''}
            name="dailyLimit"
            value={values.dailyLimit}
            type="number"
            placeholder="10 000"
            onChange={handleChange}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Monthly limit</label>
        <div>
          <Input
            className={errors.dailyLimit ? 'error' : ''}
            name="monthLimit"
            value={values.monthLimit}
            type="number"
            placeholder="10 000"
            onChange={handleChange}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Number of cards</label>
        <div>
          <Input
            className={errors.quantity ? 'error' : ''}
            name="quantity"
            value={values.quantity}
            type="number"
            placeholder="100"
            onChange={handleChange}
          />
        </div>
      </SUIForm.Field>
      <SUIForm.Field inline>
        <label>Buyer</label>
        <div>
          <select
            className={errors.buyer ? 'error' : ''}
            name="buyer"
            onBlur={({ target: { value } }) => handleChange(Number(value))}
          >
            <option value="" selected disabled>
              Select buyer...
            </option>
            {usersList.map((user) => (
              <option key={user.id} value={user.id}>
                {user.firstName} {user.lastName}
              </option>
            ))}
          </select>
        </div>
      </SUIForm.Field>
      <SUIForm.Field className="Actions">
        <Button type="submit" primary>
          Continue
        </Button>
      </SUIForm.Field>
    </SUIForm>
  );
};

export default Form;
