import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Header } from 'semantic-ui-react';

import Company from './Company';

import { ReactComponent as HomeIcon } from './imgs/homeIcon.svg';
import { ReactComponent as CardsIcon } from './imgs/cardsIcon.svg';
import { ReactComponent as BalancesIcon } from './imgs/balancesIcon.svg';
import { ReactComponent as AnalyticsIcon } from './imgs/analyticsIcon.svg';
import { ReactComponent as SettingsIcon } from './imgs/settingsIcon.svg';
import { ReactComponent as UsersIcon } from './imgs/usersIcon.svg';
import useBootstrap from './useBootstrap';
import './Sidebar.scss';

type Props = {
  appName: string;
};

const Sidebar: React.FC<Props> = ({ appName }) => {
  const { admin, adminLike } = useBootstrap();

  const { pathname } = useLocation();

  const items = [
    {
      title: 'Dashboard',
      href: '/',
      icon: <HomeIcon />,
    },
    {
      title: 'My cards',
      href: '/cards',
      icon: <CardsIcon />,
    },
    {
      title: 'Balances',
      href: '/balances',
      icon: <BalancesIcon />,
    },
    {
      title: 'Settings',
      href: '/settings',
      icon: <SettingsIcon />,
    },
  ];

  if (admin) {
    items.push({
      title: 'Analytics',
      href: '/analytics',
      icon: <AnalyticsIcon />,
    });
  }

  if (adminLike) {
    items.push({
      title: 'Users',
      href: '/users-list',
      icon: <UsersIcon />,
    });
  }

  return (
    <nav className="Sidebar">
      <Header as="h2">{appName}</Header>
      <Company />
      <ul>
        {items.map((item) => (
          <li
            key={item.title}
            className={pathname === item.href ? 'active' : ''}
          >
            <Link to={item.href}>
              {item.icon}
              {item.title}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default Sidebar;
