import React, { useEffect, useState } from 'react';
import { FaEye, FaPlus } from 'react-icons/fa';
import { Accordion, Button, Header } from 'semantic-ui-react';

// import RateModal from '../RateModal/RateModal';
import TopUpsList from '../TopUpsList/TopUpsList';

import { formatCurrency } from '../../utils/numbers';

import useBootstrap from './useBootstrap';
import './Balances.scss';

const Balances: React.FC = () => {
  const [activeIndex, setActiveIndex] = useState<string | number | undefined>(
    -1,
  );
  const { bootstrap, list } = useBootstrap();
  // const [rateModal, setRateModal] = useState(false);

  const handleClick = (index: string | number | undefined): void => {
    setActiveIndex(index === activeIndex ? -1 : index);
  };

  useEffect(() => bootstrap(), []);

  return (
    <section className="Balances">
      <header>
        <Header as="h1">Balances</Header>
        <Button primary>
          <FaPlus /> Add account
        </Button>
        {/* }
        <RateModal
          opened={rateModal}
          handleCloseModal={() => setRateModal(false)}
          handleOpenModal={() => setRateModal(true)}
        />
        { */}
      </header>
      {Boolean(list.length) && (
        <Accordion>
          {list.map((item, itemIndex) => (
            <div key={item.id}>
              <Accordion.Title
                active={activeIndex === itemIndex}
                index={itemIndex}
                onClick={(_, { index }) => handleClick(index)}
              >
                <span>{item.currency}</span>
                <span>{formatCurrency(item.amount)}</span>
                <FaEye />
              </Accordion.Title>
              <Accordion.Content active={activeIndex === itemIndex}>
                <TopUpsList title="Top up history" />
              </Accordion.Content>
            </div>
          ))}
        </Accordion>
      )}
    </section>
  );
};

export default Balances;
