import { useCallback, useState } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import qs from 'query-string';

import { usersListSelector } from '../../common/users/selectors';

import type { CardStatuses, UserDTO } from '../../api';

type ReturnType = {
  handleFilterByBuyer: (buyer?: number, name?: string) => void;
  handleFilterByDate: (date?: string) => void;
  handleFilterByStatus: (status?: CardStatuses) => void;
  buyerFilter?: string;
  statusFilter: CardStatuses;
  usersList: UserDTO[];
  endDate: Date;
  startDate: Date;
  setEndDate: (date: Date) => void;
  setStartDate: (date: Date) => void;
};

const useFilters = (): ReturnType => {
  const history = useHistory();
  const currentFilters = qs.parse(history.location.search);

  const [buyerFilter, setBuyerFilter] = useState<string | undefined>(undefined);
  const [statusFilter, setStatusFilter] = useState<CardStatuses>(undefined);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const usersList = useSelector(usersListSelector);

  const handleFilterByBuyer = useCallback(
    (buyer, name) => {
      const newSearch = { ...currentFilters };

      if (!buyer) {
        delete newSearch.buyer;
      }

      setBuyerFilter(name);

      history.push({
        search: buyer
          ? qs.stringify({ ...newSearch, buyer })
          : qs.stringify({ ...newSearch }),
      });
    },
    [currentFilters, setBuyerFilter],
  );

  const handleFilterByDate = useCallback((date) => {
    const newSearch = { ...currentFilters };

    if (!date) {
      delete newSearch.date;
    }

    setStatusFilter(date);

    history.push({
      search: date
        ? qs.stringify({ ...newSearch, date })
        : qs.stringify({ ...newSearch }),
    });
  }, []);

  const handleFilterByStatus = useCallback(
    (status) => {
      const newSearch = { ...currentFilters };

      if (!status) {
        delete newSearch.status;
      }

      setStatusFilter(status);

      history.push({
        search: status
          ? qs.stringify({ ...newSearch, status })
          : qs.stringify({ ...newSearch }),
      });
    },
    [currentFilters, setStatusFilter],
  );

  return {
    handleFilterByBuyer,
    handleFilterByDate,
    handleFilterByStatus,
    buyerFilter,
    statusFilter,
    usersList,
    endDate,
    setEndDate,
    startDate,
    setStartDate,
  };
};

export default useFilters;
