/* eslint-disable import/no-cycle */
import handlePrivateRequest from '../utils/handlePrivateRequest';
import URL from '../utils/url';

import type { Response } from '../utils/types';
import type { CreateCardType } from './types';

export const getAllCards = (
  params: { q: string } | undefined,
): Promise<Response> =>
  handlePrivateRequest({
    url: URL('cards'),
    method: 'GET',
    params,
  });

export const createCard = (data: CreateCardType): Promise<Response> =>
  handlePrivateRequest({
    url: URL('cards'),
    method: 'POST',
    data,
  });

export const getCardHistory = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`cards/${id}/history`),
    method: 'GET',
  });

export const pauseCard = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`cards/${id}/block`),
    method: 'PATCH',
  });

export const unpauseCard = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`cards/${id}/unblock`),
    method: 'PATCH',
  });

export const blockCard = (id: number): Promise<Response> =>
  handlePrivateRequest({
    url: URL(`cards/${id}`),
    method: 'DELETE',
  });

export const batchPauseCard = (cards: number[]): Promise<Response> =>
  handlePrivateRequest({
    url: URL('cards/scope-block'),
    method: 'PUT',
    data: cards,
  });

export const batchUnpauseCard = (cards: number[]): Promise<Response> =>
  handlePrivateRequest({
    url: URL('cards/scope-unblock'),
    method: 'PUT',
    data: cards,
  });

export const batchBlockCard = (cards: number[]): Promise<Response> =>
  handlePrivateRequest({
    url: URL('cards/scope-delete'),
    method: 'DELETE',
    data: cards,
  });
