import { useCallback, useState } from 'react';

import type { Notification } from '../../common/notifications/types';

type ReturnType = {
  handleCloseModal: () => void;
  handleOpenModal: () => void;
  list: Notification[];
  notificationsModal: boolean;
};

const useNotifications = (): ReturnType => {
  const [notificationsModal, setNotificationsModal] = useState(false);

  const handleCloseModal = useCallback(() => setNotificationsModal(false), []);

  const handleOpenModal = useCallback(() => setNotificationsModal(true), []);

  const list = [
    {
      type: 'warning',
      text: 'Card 5346 has expired',
      date: '2018-04-14T05:06:07',
    },
    {
      type: 'default',
      text: 'Your balance is used on 67%',
      date: '2018-01-31T05:06:07',
    },
    {
      type: 'default',
      text: 'System update available',
      date: '2018-01-31T05:06:07',
    },
    {
      type: 'settings',
      text: 'Daily system backup completed',
      date: '2018-01-31T05:06:07',
    },
    {
      type: 'warning',
      text: 'Payment was declined',
      date: '2018-04-14T05:06:07',
    },
    {
      type: 'warning',
      text: "Invoice wasn't paid",
      date: '2018-04-14T05:06:07',
    },
  ] as Notification[];

  return { handleCloseModal, handleOpenModal, list, notificationsModal };
};

export default useNotifications;
