import * as constants from './constants';

import type { StatementActionTypes, StatementsState } from './types';
import type { StatementDTO } from '../../api';

const initialState: StatementsState = {
  list: [] as StatementDTO[],
};

const reducer = (
  state = initialState,
  action: StatementActionTypes,
): StatementsState => {
  switch (action.type) {
    case constants.FETCH_STATEMENTS_LIST:
      return {
        ...state,
        list: action.payload,
      };

    default:
      return state;
  }
};

export default reducer;
