import React, { useState } from 'react';
import moment from 'moment';
import { BsClockHistory, BsPause, BsPlay, BsPlus, BsX } from 'react-icons/bs';
import { Checkbox, Table } from 'semantic-ui-react';

import { Button, Status } from '../../components';

import ViewCardModal from '../ViewCardModal/ViewCardModal';

import type { CardDTO } from '../../api';

type Props = {
  adminLike: boolean;
  item: CardDTO;
  selectedItems: number[];
  handleBlockCard: (id: number) => void;
  handlePauseCard: (id: number) => void;
  handleUnpauseCard: (id: number) => void;
  handleGetCardHistory: (id: number) => void;
  handleSelect: (id: number) => void;
};

const CardsListRow: React.FC<Props> = ({
  adminLike,
  item,
  selectedItems,
  handleBlockCard,
  handlePauseCard,
  handleUnpauseCard,
  handleGetCardHistory,
  handleSelect,
}) => {
  const [detailsModal, setDetailsModal] = useState(false);

  return (
    <Table.Row key={item.id}>
      <Table.Cell>
        <Checkbox
          checked={selectedItems.includes(item.id)}
          onClick={() => handleSelect(item.id)}
        />
      </Table.Cell>
      <Table.Cell>{item.id}</Table.Cell>
      <Table.Cell className="Date">
        {moment(item.date).format('DD/MM/YYYY')}
      </Table.Cell>
      <Table.Cell>{item.currency}</Table.Cell>
      <Table.Cell>** {item.lastDigits}</Table.Cell>
      <Table.Cell>
        {item.buyer || (
          <div className="AddBuyer">{adminLike && <BsPlus />}</div>
        )}
      </Table.Cell>
      <Table.Cell>
        <ViewCardModal
          id={item.id}
          opened={detailsModal}
          handleOpenModal={() => setDetailsModal(true)}
          handleCloseModal={() => setDetailsModal(false)}
        />
      </Table.Cell>
      <Table.Cell>
        {item.status === 'paused' ? (
          <Button
            className="BiggerButton"
            basic
            icon
            onClick={() => handleUnpauseCard(item.id)}
          >
            <BsPlay />
          </Button>
        ) : (
          <Button
            className="BiggerButton"
            basic
            icon
            onClick={() => handlePauseCard(item.id)}
          >
            <BsPause />
          </Button>
        )}
      </Table.Cell>
      <Table.Cell>
        <Button
          className="BiggerButton"
          basic
          icon
          onClick={() => handleBlockCard(item.id)}
        >
          <BsX />
        </Button>
      </Table.Cell>
      <Table.Cell>
        <Button basic icon onClick={() => handleGetCardHistory(item.id)}>
          <BsClockHistory />
        </Button>
      </Table.Cell>
      <Table.Cell>
        <Status type={item.status} />
      </Table.Cell>
    </Table.Row>
  );
};

export default CardsListRow;
