import { useCallback, useState } from 'react';
import { useRequest } from '@umijs/hooks';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';

import { restorePassword, signIn, signUp } from '../../api';

import { saveTokens } from '../../utils/tokens';

import { signInAction } from '../../common/users/actions';

import type {
  FullRegisterDTO,
  RestorePasswordType,
  SignInDTO,
  SignInType,
  SignUpDTO,
} from '../../api';
import type { RegistrationSteps } from '../../types';

type InputTypes = 'password' | 'text';

type ReturnType = {
  formValues: FullRegisterDTO;
  handleSetValues: (data: FullRegisterDTO) => void;
  handleSignInSubmit: (data: SignInType) => void;
  handleSignUpSubmit: (data: FullRegisterDTO) => void;
  handleResetSubmit: (data: RestorePasswordType) => void;
  inputType: InputTypes;
  setStep: (step: RegistrationSteps) => void;
  setType: (type: InputTypes) => void;
  step: number;
};

const useSign = (): ReturnType => {
  const dispatch = useDispatch();

  const [step, setStep] = useState<RegistrationSteps>(1);
  const [formValues, setFormValues] = useState<FullRegisterDTO>({
    businessName: '',
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    position: '',
    countryCode: '',
    phone: '',
    industry: '',
    source: '',
    customSource: '',
    expectedVolume: 0,
    expectedNumber: 0,
    mainCurrency: '',
    registrationNumber: '',
    state: '',
    country: '',
    city: '',
    postCode: '',
    address: '',
    address2: '',
  });
  const [inputType, setType] = useState<InputTypes>('password');

  const { run: handleSignUp } = useRequest<SignUpDTO>(signUp, { manual: true });

  const { run: handleSignIn } = useRequest<SignInDTO>(signIn, { manual: true });

  const { run: handleReset } = useRequest(restorePassword, { manual: true });

  const handleSignInSubmit = useCallback(
    async (data) => {
      const response = await handleSignIn(data);

      if (response.access_token) {
        saveTokens(response.access_token, response.refresh_token);
        dispatch(signInAction(true));
      } else {
        toast.error('Invalid username or password');
      }
    },
    [dispatch],
  );

  const handleSetValues = useCallback(
    (data) =>
      setFormValues({
        ...formValues,
        ...data,
      }),
    [formValues, setFormValues],
  );

  const handleSignUpSubmit = useCallback(
    async (data) => {
      const response = await handleSignUp({
        ...formValues,
        ...data,
      });

      if (response.access_token) {
        saveTokens(response.access_token, response.refresh_token);
        dispatch(signInAction(true));
      } else {
        toast.error('Something went wrong');
      }
    },
    [dispatch, formValues],
  );

  const handleResetSubmit = useCallback(
    async (data) => {
      const response = await handleReset(data);

      console.log(response);
    },
    [dispatch],
  );

  return {
    formValues,
    handleSetValues,
    handleSignInSubmit,
    handleSignUpSubmit,
    handleResetSubmit,
    inputType,
    setStep,
    setType,
    step,
  };
};

export default useSign;
