import { createSelector } from 'reselect';

import { GlobalState } from '../../types';

import { AppState } from './types';

export const appSelector = (state: GlobalState): AppState => state.app;

export const appNameSelector = createSelector(appSelector, (app) => app.name);

export const appThemeSelector = createSelector(appSelector, (app) => app.theme);

export const appIsLoadingSelector = createSelector(
  appSelector,
  (app) => app.isLoading,
);
