import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';

import { exportStatement } from '../../api';
import type { ExportStatementTypes } from '../../api';

type ReturnType = {
  handleExportStatement: (type: ExportStatementTypes) => void;
};

const useExport = (): ReturnType => {
  const { run: handleExport } = useRequest(exportStatement, { manual: true });

  const handleExportStatement = useCallback(async (type) => {
    const data = await handleExport({ type });

    if (data) {
      console.log('Success');
    }
  }, []);

  return {
    handleExportStatement,
  };
};

export default useExport;
