import { useSelector } from 'react-redux';

import {
  isUserAdminSelector,
  isUserLeadSelector,
  isUserRootSelector,
} from '../../common/users/selectors';

type ReturnType = {
  adminLike: boolean;
};

const useRoles = (): ReturnType => {
  const isAdmin = useSelector(isUserAdminSelector);
  const isLead = useSelector(isUserLeadSelector);
  const isRoot = useSelector(isUserRootSelector);

  const adminLike = isAdmin || isLead || isRoot;

  return { adminLike };
};

export default useRoles;
