import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { InviteUserType, inviteUser } from '../../api';

import useBootstrap from './useBootstrap';

type ReturnType = {
  handleInviteBuyer: (user: InviteUserType) => void;
};

const useInviteBuyer = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const { run: handleInvite } = useRequest(inviteUser, { manual: true });

  const handleInviteBuyer = useCallback(async (user) => {
    const data = await handleInvite(user);

    if (data) {
      toast.success('User invited successfully');
      bootstrap();
    } else {
      toast.error('Cannot invite user');
    }
  }, []);

  return { handleInviteBuyer };
};

export default useInviteBuyer;
