import { useCallback } from 'react';
import { useFormik } from 'formik';

type SettingsDTO = {
  businessName: string;
  name: string;
  email: string;
  phone: string;
  emailNotification: boolean;
  telegramNotification: boolean;
};

type ReturnType = {
  onChange: ({ key, value }: { key: string; value: string | boolean }) => void;
  values: SettingsDTO;
};

const useForm = (): ReturnType => {
  const initialValues = {
    businessName: 'Wayne Inc.',
    name: 'Bruce Wayne',
    email: 'bruceWayne@mail.com',
    phone: '992-695-1715',
    emailNotification: false,
    telegramNotification: true,
  };

  const { setValues, values } = useFormik({
    initialValues,
    onSubmit: () => {},
  });

  const onChange = useCallback(
    ({ key, value }) => {
      console.log(key);
      console.log(value);

      setValues({
        ...values,
        [key]: value,
      });

      // dispatch saving...
    },
    [setValues, values],
  );

  return { onChange, values };
};

export default useForm;
