import React from 'react';
import { Link } from 'react-router-dom';
import cn from 'classnames';
import { Header } from 'semantic-ui-react';

import './Card.scss';

type Props = {
  children: React.ReactNode;
  className?: string;
  isPadded?: boolean;
  link?: string;
  linkText?: string;
  title?: string;
  titleDescription?: string;
  titleSize?: 'small' | 'big';
};

const Card: React.FC<Props> = ({
  children,
  className = '',
  isPadded,
  link = '',
  linkText = 'See more info',
  title,
  titleDescription = '',
  titleSize = 'small',
}) => {
  const classes = cn('Card', className);
  const titleClasses = cn('Title', titleSize);

  return (
    <div className={classes}>
      {Boolean(title) && (
        <Header className={titleClasses} as="h3">
          {title}
          {titleDescription && <span>{titleDescription}</span>}
        </Header>
      )}
      {isPadded ? <section className="Content">{children}</section> : children}
      {Boolean(link) && (
        <section className="Link">
          <Link to={link}>{linkText}</Link>
        </section>
      )}
    </div>
  );
};

export default Card;
