import React from 'react';
import moment from 'moment';
import { Header } from 'semantic-ui-react';

import { Badge } from '../../components';

import type { TopUp } from '../../types';

import { formatCurrency } from '../../utils/numbers';

import './TopUpsList.scss';

type Props = {
  items?: TopUp[];
  title: string;
};

const TopUpsList: React.FC<Props> = ({ items = [], title }) => (
  <section className="TopUpsList">
    <Header className="Header" as="h3">
      {title}
    </Header>
    <ul>
      {items.map((item) => (
        <li key={item.id}>
          <span className="Status">
            <Badge type={item.status} />
          </span>
          <span className="Date">
            {moment(item.date).format('HH:mm, DD MMMM YYYY')}
          </span>
          <span className="Amount">+ {formatCurrency(item.amount)}</span>
          <span className="Company">{item.company}</span>
          <span>
            Amet minim mollit non deserunt ullamco est sit aliqua dolor do .
          </span>
        </li>
      ))}
    </ul>
  </section>
);

export default TopUpsList;
