import React, { useEffect } from 'react';
import { Header } from 'semantic-ui-react';

import InviteBuyerModal from '../InviteBuyerModal/InviteBuyerModal';
import UsersList from '../UsersList/UsersList';

import useBootstrap from './useBootstrap';
import useInviteBuyer from './useInviteBuyer';
import './Users.scss';

const Users: React.FC = () => {
  const { bootstrap, list } = useBootstrap();
  const { handleInviteBuyer } = useInviteBuyer();

  useEffect(() => {
    if (!list.length) {
      bootstrap();
    }
  }, []);

  return (
    <section className="Users">
      <header>
        <Header as="h1">Users</Header>
        <InviteBuyerModal handleCreateUser={handleInviteBuyer} />
      </header>
      <UsersList items={list} />
    </section>
  );
};

export default Users;
