import React from 'react';
import { Table } from 'semantic-ui-react';

import { UserDTO } from '../../api';

import { TABLE_COLS } from './constants';
import UsersListRow from './UsersListRow';

import useBlocking from './useBlocking';
import useEditUser from './useEditUser';
import useRole from './useRole';
import './UsersList.scss';

type Props = {
  items: UserDTO[];
};

const UsersList: React.FC<Props> = ({ items }) => {
  const { blockUser, unblockUser } = useBlocking();
  const { editUser } = useEditUser();
  const { setUserRole } = useRole();

  return (
    <div className="UsersList">
      <Table>
        <Table.Header>
          <Table.Row>
            {TABLE_COLS.map((name) => (
              <Table.HeaderCell key={name}>{name}</Table.HeaderCell>
            ))}
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {items
            .sort((a, b) => (a.id > b.id ? 1 : b.id > a.id ? -1 : 0))
            .map((item) => (
              <UsersListRow
                key={item.id}
                user={item}
                handleBlockUser={blockUser}
                handleUnblockUser={unblockUser}
                handleSetUserRole={setUserRole}
                handleEditUser={editUser}
              />
            ))}
        </Table.Body>
      </Table>
    </div>
  );
};

export default UsersList;
