import { useCallback, useState } from 'react';

type ReturnType = {
  selectedIds: number[];
  select: (id: number) => void;
  selectAll: () => void;
};

const useSelection = (list: number[]): ReturnType => {
  const [selectedIds, setSelectedId] = useState<number[]>([]);

  const select = useCallback(
    (id) => {
      if (selectedIds.includes(id)) {
        setSelectedId(selectedIds.filter((sId) => sId !== id));
      } else {
        setSelectedId([...selectedIds, id]);
      }
    },
    [selectedIds, setSelectedId],
  );

  const selectAll = useCallback(() => {
    if (selectedIds.length !== list.length) {
      setSelectedId(list);
    } else {
      setSelectedId([]);
    }
  }, [list, selectedIds, setSelectedId]);

  return { selectedIds, select, selectAll };
};

export default useSelection;
