import React from 'react';
import { FaPlus, FaRegTimesCircle } from 'react-icons/fa';
import { Button, Modal } from 'semantic-ui-react';

import Form from './Form';

import type { CreateCardType } from '../../api';

type Props = {
  opened: boolean;
  handleOpenModal: () => void;
  handleCloseModal: () => void;
  handleCreateNewCard: (data: CreateCardType) => void;
};

const CreateCardModal: React.FC<Props> = ({
  opened,
  handleCloseModal,
  handleOpenModal,
  handleCreateNewCard,
}) => {
  return (
    <Modal
      open={opened}
      size="small"
      trigger={
        <Button primary>
          <FaPlus /> Add new
        </Button>
      }
      closeIcon={<FaRegTimesCircle />}
      onClose={handleCloseModal}
      onOpen={handleOpenModal}
    >
      <Modal.Header>Create a new card</Modal.Header>
      <Modal.Content>
        <Form handleCreateNewCard={handleCreateNewCard} />
      </Modal.Content>
    </Modal>
  );
};

export default CreateCardModal;
