import React from 'react';
import {
  Area,
  AreaChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

import './BarChart.scss';

const BarChart: React.FC = () => {
  const data = [
    {
      day: 11,
      spend: 35.84,
    },
    {
      day: 12,
      spend: 1215.68,
    },
    {
      day: 13,
      spend: 1652.93,
    },
    {
      day: 14,
      spend: 161.65,
    },
    {
      day: 15,
      spend: 226.16,
    },
    {
      day: 16,
      spend: 574.86,
    },
    {
      day: 17,
      spend: 435.65,
    },
    {
      day: 19,
      spend: 1683.49,
    },
    {
      day: 20,
      spend: 305.92,
    },
    {
      day: 21,
      spend: 1548.89,
    },
    {
      day: 22,
      spend: 117.8,
    },
    {
      day: 23,
      spend: 403.18,
    },
    {
      day: 24,
      spend: 24.73,
    },
  ];

  const getMaxAxisValue = (array: number[], percents: number): number => {
    const maxValue = Math.max.apply(null, array);
    const maxAxisValue = Math.round(maxValue + (maxValue / 100) * percents);

    return Math.round(maxAxisValue / 100) * 100;
  };

  const getMinValueInPercents = (array: number[]): number =>
    Math.round(
      (Math.min.apply(null, array) / Math.max.apply(null, array)) * 100,
    );

  return (
    <section className="BarChart">
      <ResponsiveContainer height="100%" width="100%">
        <AreaChart
          width={730}
          height={250}
          data={data}
          margin={{ top: 0, right: 0, left: 0, bottom: 0 }}
        >
          <defs>
            <linearGradient id="colorSpend" x1="0" y1="0" x2="0" y2="1">
              <stop offset="15%" stopColor="#1eff6a" stopOpacity={0.3} />
              <stop
                offset={`${
                  100 - getMinValueInPercents(data.map((item) => item.spend))
                }%`}
                stopColor="#1eff6a"
                stopOpacity={0}
              />
            </linearGradient>
          </defs>
          <XAxis dataKey="day" />
          <YAxis
            yAxisId="spendYAxis"
            dataKey="spend"
            orientation="left"
            domain={[
              0,
              getMaxAxisValue(
                data.map((item) => item.spend),
                40,
              ),
            ]}
          />
          <CartesianGrid
            strokeDasharray="0"
            stroke="#556583"
            vertical={false}
          />
          <Tooltip />
          <Area
            yAxisId="spendYAxis"
            type="monotone"
            dataKey="spend"
            stroke="#1eff6a"
            fillOpacity={1}
            fill="url(#colorSpend)"
            activeDot={{
              stroke: '#1eff6a',
              strokeWidth: 2,
              r: 4,
            }}
            unit="$"
          />
        </AreaChart>
      </ResponsiveContainer>
    </section>
  );
};

export default BarChart;
