import * as constants from './constants';

import type { CardActionTypes, CardsState } from './types';
import type { CardDTO } from '../../api';

export const initialState: CardsState = {
  cardHistory: {},
  list: [] as CardDTO[],
};

const reducer = (state = initialState, action: CardActionTypes): CardsState => {
  switch (action.type) {
    case constants.FETCH_CARDS_LIST:
      return {
        ...state,
        list: action.payload,
      };

    default:
      return state;
  }
};

export default reducer;
