import { combineReducers } from 'redux';

import appReducer from '../common/app/reducer';
import cardsReducer from '../common/cards/reducer';
import statementsReducer from '../common/statements/reducer';
import usersReducer from '../common/users/reducer';

export default combineReducers({
  app: appReducer,
  cards: cardsReducer,
  statements: statementsReducer,
  user: usersReducer,
});
