import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import {
  batchBlockCard,
  batchPauseCard,
  batchUnpauseCard,
  blockCard,
  getCardHistory,
  pauseCard,
  unpauseCard,
} from '../../api';

import useBootstrap from './useBootstrap';

type ReturnType = {
  batchBlock: (ids: number[]) => void;
  batchPause: (ids: number[]) => void;
  batchUnpause: (ids: number[]) => void;
  block: (id: number) => void;
  getHistory: (id: number) => void;
  pause: (id: number) => void;
  unpause: (id: number) => void;
};

const useActions = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const { run: handleFetchCardHistory } = useRequest(getCardHistory, {
    manual: true,
  });
  const { run: handlePauseCard } = useRequest(pauseCard, {
    manual: true,
  });
  const { run: handleUnpauseCard } = useRequest(unpauseCard, {
    manual: true,
  });
  const { run: handleBlockCard } = useRequest(blockCard, {
    manual: true,
  });
  const { run: handleBatchPauseCard } = useRequest(batchPauseCard, {
    manual: true,
  });
  const { run: handleBatchUnpauseCard } = useRequest(batchUnpauseCard, {
    manual: true,
  });
  const { run: handleBatchBlockCard } = useRequest(batchBlockCard, {
    manual: true,
  });

  const getHistory = useCallback(async (id) => {
    const data = await handleFetchCardHistory(id);

    console.log(data);

    if (data) {
      console.log(data); // Dispatch
    } else {
      toast.error('Cannot fetch card history');
    }
  }, []);

  const pause = useCallback(async (id) => {
    const data = await handlePauseCard(id);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot pause card');
    }
  }, []);

  const unpause = useCallback(async (id) => {
    const data = await handleUnpauseCard(id);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot unpause card');
    }
  }, []);

  const block = useCallback(async (id) => {
    const data = await handleBlockCard(id);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot block card');
    }
  }, []);

  const batchPause = useCallback(async (ids) => {
    const data = await handleBatchPauseCard(ids);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot pause selected cards');
    }
  }, []);

  const batchUnpause = useCallback(async (ids) => {
    const data = await handleBatchUnpauseCard(ids);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot unpause selected cards');
    }
  }, []);

  const batchBlock = useCallback(async (ids) => {
    const data = await handleBatchBlockCard(ids);

    console.log(data);

    if (data) {
      bootstrap();
    } else {
      toast.error('Cannot block selected cards');
    }
  }, []);

  return {
    batchBlock,
    batchPause,
    batchUnpause,
    block,
    getHistory,
    pause,
    unpause,
  };
};

export default useActions;
