import React from 'react';

import { Checkbox, Table } from 'semantic-ui-react';

import CardsListRow from './CardsListRow';

import { TABLE_COLS } from './constants';
import './CardsList.scss';

import type { CardDTO } from '../../api';

type Props = {
  adminLike: boolean;
  items?: CardDTO[];
  selectedItems: number[];
  handleBlockCard: (id: number) => void;
  handlePauseCard: (id: number) => void;
  handleUnpauseCard: (id: number) => void;
  handleGetCardHistory: (id: number) => void;
  handleSelect: (id: number) => void;
  handleSelectAll: () => void;
};

const CardsList: React.FC<Props> = ({
  adminLike,
  items = [],
  selectedItems,
  handleBlockCard,
  handlePauseCard,
  handleUnpauseCard,
  handleGetCardHistory,
  handleSelect,
  handleSelectAll,
}) => {
  return (
    <div className="CardsList">
      <Table>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>
              <Checkbox
                checked={
                  Boolean(items.length) && selectedItems.length === items.length
                }
                disabled={!items.length}
                onClick={handleSelectAll}
              />
            </Table.HeaderCell>
            {TABLE_COLS.map((name) => (
              <Table.HeaderCell key={name}>{name}</Table.HeaderCell>
            ))}
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {items.map((item) => (
            <CardsListRow
              key={item.id}
              adminLike={adminLike}
              item={item}
              selectedItems={selectedItems}
              handleBlockCard={handleBlockCard}
              handlePauseCard={handlePauseCard}
              handleUnpauseCard={handleUnpauseCard}
              handleGetCardHistory={handleGetCardHistory}
              handleSelect={handleSelect}
            />
          ))}
        </Table.Body>
      </Table>
    </div>
  );
};

export default CardsList;
