import { createSelector } from 'reselect';

import { USER_ROLES } from '../../api';

import { toCamelCase } from '../../utils/strings';

import type { GlobalState } from '../../types';
import type { UsersState } from './types';

export const userSelector = (state: GlobalState): UsersState => state.user;

export const isLoggedSelector = createSelector(
  userSelector,
  (user) => user.isLogged,
);

export const usersListSelector = createSelector(
  userSelector,
  (user) => user.list,
);

export const userDataSelector = createSelector(
  userSelector,
  (user) => user.user,
);

export const isUserAdminSelector = createSelector(
  userSelector,
  (user) => user.user && user.user.role === toCamelCase(USER_ROLES[0]),
);

export const isUserRootSelector = createSelector(
  userSelector,
  (user) => user.user && user.user.role === toCamelCase(USER_ROLES[1]),
);

export const isUserLeadSelector = createSelector(
  userSelector,
  (user) => user.user && user.user.role === toCamelCase(USER_ROLES[2]),
);

export const isUserTrustedSelector = createSelector(
  userSelector,
  (user) => user.user && user.user.role === toCamelCase(USER_ROLES[3]),
);

export const isUserBuyerSelector = createSelector(
  userSelector,
  (user) => user.user && user.user.role === toCamelCase(USER_ROLES[4]),
);

export const isUserHaveAdditionalRights = createSelector(
  isUserAdminSelector,
  isUserLeadSelector,
  isUserRootSelector,
  (isAdmin, isLead, isRoot) => isAdmin || isLead || isRoot,
);
