/* eslint-disable-next-line */
export const formatCurrency = (
  amount: number,
  params: { currency: string; locale?: string } = {
    currency: 'USD',
    locale: 'en-US',
  },
): string =>
  new Intl.NumberFormat(params.locale, {
    style: 'currency',
    currency: params.currency,
    minimumFractionDigits: 0,
    maximumFractionDigits: 2,
  }).format(amount);
