import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { getBalances } from '../../api';
import type { BalancesDTO } from '../../api';

type ReturnType = {
  bootstrap: () => void;
  list: BalancesDTO[];
};

const useBootstrap = (): ReturnType => {
  const list = [
    { id: 0, currency: 'USD', amount: 120, user: 0 },
    { id: 1, currency: 'EUR', amount: 120, user: 0 },
  ] as BalancesDTO[];

  const { run: handleFetchBalances } = useRequest<BalancesDTO[]>(getBalances, {
    manual: true,
  });

  const bootstrap = useCallback(async () => {
    const response = await handleFetchBalances();

    if (response) {
      console.log(response);
    } else {
      toast.error('Balances fetch error');
    }
  }, []);

  return { bootstrap, list };
};

export default useBootstrap;
