import React, { useState } from 'react';
import { FaPencilAlt, FaTimesCircle } from 'react-icons/fa';
import { useFormik } from 'formik';
import { Button, Form, Input, Modal } from 'semantic-ui-react';

import type { EditUserType, UserDTO } from '../../api';

import './EditUserModal.scss';

type Props = {
  user: UserDTO;
  handleEditUser: (data: EditUserType) => void;
};

const EditUserModal: React.FC<Props> = ({ user, handleEditUser }) => {
  const [open, setOpen] = useState(false);

  const initialValues = {
    id: user.id,
    firstName: user.firstName,
    lastName: user.lastName,
    email: user.email,
  };

  const { handleChange, handleSubmit, values } = useFormik({
    initialValues,
    onSubmit: (data) => {
      handleEditUser(data);
      setOpen(false);
    },
  });

  return (
    <Modal
      className="EditUserModal"
      open={open}
      size="small"
      trigger={<FaPencilAlt />}
      closeIcon={<FaTimesCircle />}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
    >
      <Modal.Header>Edit user</Modal.Header>
      <Modal.Content>
        <Form onSubmit={handleSubmit}>
          <Form.Field>
            <Input
              className={values.firstName ? 'filled' : ''}
              label="First name"
              labelPosition="right"
              type="text"
              name="firstName"
              value={values.firstName}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <Input
              className={values.lastName ? 'filled' : ''}
              label="Last name"
              labelPosition="right"
              type="text"
              name="lastName"
              value={values.lastName}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <Input
              className={values.email ? 'filled' : ''}
              label="Email"
              labelPosition="right"
              type="email"
              name="email"
              value={values.email}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field className="ButtonHolder">
            <Button basic primary onClick={() => setOpen(false)}>
              Cancel
            </Button>
            <Button type="submit" primary>
              Submit
            </Button>
          </Form.Field>
        </Form>
      </Modal.Content>
    </Modal>
  );
};

export default EditUserModal;
