import React from 'react';
import { useFormik } from 'formik';
import { Button, Form, Header, Input, Segment } from 'semantic-ui-react';

import useSign from './useSign';
import './Sign.scss';

const PasswordRestore: React.FC = () => {
  const { handleResetSubmit } = useSign();

  const initialValues = {
    email: '',
  };

  const { values, handleChange, handleSubmit } = useFormik({
    initialValues,
    onSubmit: (data) => handleResetSubmit(data),
  });

  return (
    <Segment basic>
      <div />
      <section className="Form">
        <Header as="h1">Forgot Password</Header>
        <p className="Lead">
          Place your email in the field below.
          <br />
          We will send you a link with the instructions to follow.
        </p>
        <Form onSubmit={handleSubmit}>
          <Form.Field>
            <Input
              name="email"
              type="email"
              placeholder="Email"
              value={values.email}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field className="ButtonHolder">
            <Button primary fluid>
              Recover password
            </Button>
          </Form.Field>
        </Form>
      </section>
      <section />
    </Segment>
  );
};

export default PasswordRestore;
