/* eslint-disable import/no-cycle */
import handlePrivateRequest from '../utils/handlePrivateRequest';
import URL from '../utils/url';

import type { Response } from '../utils/types';
import type { ExportStatementType } from './types';

export const getAllStatements = (): Promise<Response> =>
  handlePrivateRequest({
    url: URL('statements'),
    method: 'GET',
  });

export const exportStatement = ({
  type,
}: ExportStatementType): Promise<Response> =>
  handlePrivateRequest({
    url: URL('statements/export'),
    method: 'POST',
    data: {
      type,
    },
  });
