import { useCallback } from 'react';
import { useRequest } from '@umijs/hooks';
import { toast } from 'react-toastify';

import { SetUserRoleType, UserDTO, setUserRoleById } from '../../api';

import useBootstrap from '../Users/useBootstrap';

type ReturnType = {
  setUserRole: (data: SetUserRoleType) => void;
};

const useRole = (): ReturnType => {
  const { bootstrap } = useBootstrap();

  const { run: handleSetRole } = useRequest<UserDTO>(setUserRoleById, {
    manual: true,
  });

  const setUserRole = useCallback(async (id) => {
    const response = await handleSetRole(id);

    if (response.id) {
      toast.success('User role changed successfully');
      bootstrap();
    } else {
      toast.error('Cannot set user role');
    }
  }, []);

  return {
    setUserRole,
  };
};

export default useRole;
