import React, { useState } from 'react';
import { FaRegTimesCircle } from 'react-icons/fa';
import { BsEye } from 'react-icons/bs';
import { Modal } from 'semantic-ui-react';

import { Button } from '../../components';

import TypePassword from './TypePassword';
import ForgotPassword from './ForgotPassword';
import CardDetails from './CardDetails';

import './ViewCardModal.scss';

type Steps = 'typePassword' | 'forgotPassword' | 'details';

type Props = {
  id: number;
  opened: boolean;
  handleOpenModal: () => void;
  handleCloseModal: () => void;
};

const ViewCardModal: React.FC<Props> = ({
  id,
  opened,
  handleOpenModal,
  handleCloseModal,
}) => {
  const [step, setStep] = useState<Steps>('typePassword');

  const content = () => {
    switch (step) {
      case 'details':
        return <CardDetails id={id} />;

      case 'forgotPassword':
        return (
          <ForgotPassword
            handleTypePasswordClick={() => setStep('typePassword')}
          />
        );

      case 'typePassword':
        return (
          <TypePassword
            handleForgotPasswordClick={() => setStep('forgotPassword')}
            handleNextStepClick={() => setStep('details')}
          />
        );

      default:
        return (
          <TypePassword
            handleForgotPasswordClick={() => setStep('forgotPassword')}
            handleNextStepClick={() => setStep('details')}
          />
        );
    }
  };

  return (
    <Modal
      className="ViewCardModal"
      open={opened}
      size="small"
      trigger={
        <Button basic icon>
          <BsEye />
        </Button>
      }
      closeIcon={<FaRegTimesCircle />}
      onClose={handleCloseModal}
      onOpen={handleOpenModal}
    >
      <Modal.Header>Card details</Modal.Header>
      <Modal.Content>{content()}</Modal.Content>
    </Modal>
  );
};

export default ViewCardModal;
