import React from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import PhoneInput, { parsePhoneNumber } from 'react-phone-number-input';
import { Button, Form, Input } from 'semantic-ui-react';

import {
  CURRENCY_OPTIONS,
  INDUSTRY_OPTIONS,
  SOURCE_OPTIONS,
} from '../../constants';

import { FullRegisterDTO } from '../../api';
import { RegistrationSteps } from '../../types';

type Props = {
  setFormValues: (data: FullRegisterDTO) => void;
  setStep: (step: RegistrationSteps) => void;
};

const SignUp: React.FC<Props> = ({ setFormValues, setStep }) => {
  const initialValues = {
    countryCode: '',
    phone: '',
    fullPhone: '+',
    industry: '',
    source: '',
    customSource: '',
    expectedVolume: undefined,
    expectedNumber: undefined,
    mainCurrency: '',
  };

  const { errors, touched, values, handleChange, handleSubmit, setValues } =
    useFormik({
      initialValues,
      validationSchema: yup.object({
        countryCode: yup.string().required(),
        phone: yup.string().required(),
        fullPhone: yup.string(),
        industry: yup.string().min(3).required(),
        source: yup.string().min(3).required(),
        customSource: yup.string(),
        expectedVolume: yup.number().required(),
        expectedNumber: yup.number().required(),
        mainCurrency: yup.string().min(2).max(3).required(),
      }),
      onSubmit: (data) => {
        setFormValues(data);
        setStep(3);
      },
    });

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Field>
        <div className="ui right labeled input filled phone">
          <PhoneInput
            value={values.fullPhone}
            onChange={(value) => {
              const parsed = parsePhoneNumber(value || '');
              setValues({
                ...values,
                countryCode: parsed ? parsed.countryCallingCode.toString() : '',
                phone: parsed ? parsed.nationalNumber.toString() : '',
                fullPhone: value,
              });
            }}
          />
          <div className="ui label label">Phone</div>
        </div>
      </Form.Field>
      <Form.Field>
        <select
          className={touched.industry && errors.industry ? 'error' : ''}
          name="industry"
          placeholder="Industry"
          defaultValue=""
          onBlur={handleChange}
        >
          <option value="" disabled>
            Select industry...
          </option>
          {INDUSTRY_OPTIONS.map((option) => (
            <option key={option.key} value={option.value}>
              {option.text}
            </option>
          ))}
        </select>
      </Form.Field>
      <Form.Field>
        {/* eslint-disable-next-line */}
        <select
          className={touched.source && errors.source ? 'error' : ''}
          name="source"
          placeholder="Source of funds"
          defaultValue=""
          onChange={handleChange}
        >
          <option value="" disabled>
            Select source...
          </option>
          {SOURCE_OPTIONS.map((option) => (
            <option key={option.key} value={option.value}>
              {option.text}
            </option>
          ))}
        </select>
      </Form.Field>
      {values.source === 'OTHER' && (
        <Form.Field>
          <Input
            className={values.customSource ? 'filled' : ''}
            label="Type source"
            labelPosition="right"
            name="customSource"
            type="text"
            error={Boolean(touched.customSource && errors.customSource)}
            value={values.customSource}
            onChange={handleChange}
          />
        </Form.Field>
      )}
      <Form.Field>
        <Input
          className={values.expectedVolume ? 'filled' : ''}
          label="Expected volume"
          labelPosition="right"
          name="expectedVolume"
          type="number"
          error={Boolean(touched.expectedVolume && errors.expectedVolume)}
          value={values.expectedVolume}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <Input
          className={values.expectedNumber ? 'filled' : ''}
          label="Expected number of cards"
          labelPosition="right"
          name="expectedNumber"
          type="number"
          error={Boolean(touched.expectedNumber && errors.expectedNumber)}
          value={values.expectedNumber}
          onChange={handleChange}
        />
      </Form.Field>
      <Form.Field>
        <select
          name="mainCurrency"
          placeholder="Choose account main currency"
          defaultValue=""
          onBlur={handleChange}
        >
          <option value="" disabled>
            Choose account main currency
          </option>
          {CURRENCY_OPTIONS.map((option) => (
            <option key={option.key} value={option.value}>
              {option.text}
            </option>
          ))}
        </select>
      </Form.Field>
      <Form.Field className="ButtonHolder">
        <Button type="submit" primary fluid>
          Sign up
        </Button>
      </Form.Field>
    </Form>
  );
};

export default SignUp;
