import React, { useState } from 'react';
import { FaTimesCircle } from 'react-icons/fa';
import { useFormik } from 'formik';
import { Button, Form, Input, Modal } from 'semantic-ui-react';

import { EDITABLE_USER_ROLES } from '../../api';

import { toCamelCase } from '../../utils/strings';

import type { InviteUserType, UserRoles } from '../../api';

import './InviteBuyerModal.scss';

type Props = {
  handleCreateUser: (data: InviteUserType) => void;
};

const InviteBuyerModal: React.FC<Props> = ({ handleCreateUser }) => {
  const [open, setOpen] = useState(false);

  const initialValues = {
    firstName: '',
    lastName: '',
    email: '',
    role: 'Buyer' as UserRoles,
  };

  const { handleChange, handleSubmit, values } = useFormik({
    initialValues,
    onSubmit: (data) => {
      handleCreateUser(data);
      setOpen(false);
    },
  });

  return (
    <Modal
      className="InviteBuyerModal"
      open={open}
      size="tiny"
      trigger={<Button primary>Invite</Button>}
      closeIcon={<FaTimesCircle />}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
    >
      <Modal.Header>Invite buyer</Modal.Header>
      <Modal.Content>
        <Form onSubmit={handleSubmit}>
          <Form.Field>
            <Input
              className={values.firstName ? 'filled' : ''}
              label="First name"
              labelPosition="right"
              type="text"
              name="firstName"
              value={values.firstName}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <Input
              className={values.lastName ? 'filled' : ''}
              label="Last name"
              labelPosition="right"
              type="text"
              name="lastName"
              value={values.lastName}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <Input
              className={values.email ? 'filled' : ''}
              label="Email"
              labelPosition="right"
              type="email"
              name="email"
              value={values.email}
              onChange={handleChange}
            />
          </Form.Field>
          <Form.Field>
            <select
              name="role"
              defaultValue={toCamelCase(values.role)}
              onChange={handleChange}
            >
              {EDITABLE_USER_ROLES.map((role) => (
                <option key={role} value={toCamelCase(role)}>
                  {role}
                </option>
              ))}
            </select>
          </Form.Field>
          <Form.Field className="ButtonHolder">
            <Button basic primary onClick={() => setOpen(false)}>
              Cancel
            </Button>
            <Button type="submit" primary>
              Invite
            </Button>
          </Form.Field>
        </Form>
      </Modal.Content>
    </Modal>
  );
};

export default InviteBuyerModal;
